import {mapMutations} from "vuex"

export default {
	data() {
		return {
			item: {},
			scrollH:0
		};
	},
	methods: {
		...mapMutations([
			'updateMerchantInfo'
		]),
		loadData(merchantID, scrollH) {
			var _self = this
			_self.scrollH = scrollH
			let url = '/api/merchant/Intro';
			let postData = { id: merchantID };
			_self.$H.post(url, postData).then(res => {
				console.log(res);
				_self.item = res.data;
				_self.updateMerchantInfo(res.data)
			});
		}
	}
};
